<template>
	<view class='container'>
		<view class="padding">
			<view class="title">反馈详情</view>
			<view class="info">
				<view class="row flex-row-between" v-if="feedbackInfo.carTypeName">
					<view class="key">关联订单</view>
					<view class='value flex-row-between text-ellipsis'>
						<text class='txt text-ellipsis'>{{feedbackInfo.carTypeName}}</text>
						<text class='txt text-ellipsis'>{{feedbackInfo.store4sName}}</text>
					</view>
				</view>
				<view class="row flex-row-between">
				<view class="key">问题类型</view><view class='value'>{{feedbackInfo.feedbackTypeStr}}</view>
				</view>
				<view class="row flex-row-between-start">
					<view class="key">投诉/建议</view><view class='value result'>{{feedbackInfo.problem}}</view>
				</view>
				<view class="row flex-row-between">
					<view class="key">反馈时间</view><view class='value'>{{feedbackInfo.createTimeStr}}</view>
				</view>
				<view class="row flex-row-between">
					<view class="key">反馈状态</view><view class='value'>{{feedbackInfo.state==0?'待处理':'已处理'}}</view>
				</view>
				<view class="row flex-row-start-start" v-if="affixList.length>0">
					<view class="key">凭证预览</view>
					<view class='value'>
						<view class="upload_w">
							<view class="upload">
								<view class="item" v-for='(litt,idx) in affixList' :key='idx'>
									<view @click='lookAffix(litt)' class='inner_w'>
										<image :src="litt" class='pic'/>
									</view>
								</view>
							</view>
						</view>
					</view>
				</view>
			</view>
			<!-- 反馈人信息 -->
			<block>
				<view class="title">反馈人信息</view>
				<view class="info">
					<view class="row flex-row-between">
						<view class="key">姓名</view><view class='value'>{{feedbackInfo.userName}}</view>
					</view>
					<view class="row flex-row-between">
						<view class="key">联系电话</view><view class='value'>{{feedbackInfo.userMobile}}</view>
					</view>
					<view class="row flex-row-between">
						<view class="key">反馈人身份</view><view class='value'>{{feedbackInfo.userTypeStr}}</view>
					</view>
				</view>
			</block>
			
			<!-- 项目组管理员 课件-->
			<block v-if='adminRole==2'>
				<view class="title">党支部信息</view>
				<view class="info">
					<view class="row flex-row-between">
						<view class="key">管理员姓名</view><view class='value'>{{feedbackInfo.partyHandler?feedbackInfo.partyHandler:''}}</view>
					</view>
					<view class="row flex-row-between">
						<view class="key">管理员电话</view><view class='value'>
							{{feedbackInfo.partyMobile?feedbackInfo.partyMobile:''}}
						</view>
					</view>
					<view class="row flex-row-between">
						<view class="key">党支部名称</view>
						<view class='value'>{{feedbackInfo.partyDeptName?feedbackInfo.partyDeptName:''}}</view>
					</view>
				</view>
			</block>
			
			
		</view>
		<view class="title padding">反馈进度</view>
		<view class="progress_w">
			<view class="cu-timeline" v-for="(item,index) in progressList" :key="index">
				<view class="cu-item text-blue">
					<view class="look_car time_item">
						<view class="timeline_title flex-row-start">{{item.nodeStepStr}}</view>
						<view class="select_date">{{item.opTimeStr}}</view>
					</view>
				</view>
			</view>
		</view>
		
		<view class="padding">
			<view class="result_w" v-if="feedbackInfo.reply">
				<view class="title">处理结果</view>
				<view class="txt padding">
					{{feedbackInfo.reply}}
				</view>
			</view>
			
			<view class="rate_wrapper" v-if="feedbackInfo.state==1 && userInfo.type!=1">
				<view class="title">处理评分</view>
				<view class="rate_w" v-if="feedbackInfo.evaluateList.length>0">
					<view class="rate_item" v-for="(item,index) in rateList" :key="index">
						<Evaluate
							:size="36" 
							class="rate" 
							:margin="'0 15'" 
							:value="item.value" 
							:text="item.name" 
							:disabled="true"
							:textColor="'#0096EA'"
							:star_fill="'/static/blue_start.png'"
						/>
					</view>
				</view>
				<view class="make_sure" @click="goRate" v-else>
					<button :class='["cu-btn","block","text-qxblue"]'>去评价</button>
				</view>
			</view>
		</view>
		<!-- 评价 弹框-->
		<view class="cu-modal rate_dialog" :class="rateDialog?'show':''" catchtouchmove="return">
			<view class="cu-dialog padding">
				<view class="content text-left">
					<view class="title">服务评价</view>
					<view class="rate_w">
						<view class="rate_item" v-for="(item,index) in rateList" :key="index">
							<Evaluate 
								@change="startChange($event,item)" 
								:value="item.value" 
								:size="36" 
								class="rate"
								:textColor="'#0096EA'"
								:star_fill="'/static/blue_start.png'"
								:margin="'0 15'"
								:text="item.name"
							/>
						</view>
					</view>
				</view>
				<view class="action margin-0 flex-sub" @click="makeScore">
					<button class="cu-btn make_sure">确认评分</button>
				</view>
			</view>
		</view>
		
		<!-- 图片查看 -->
		<view class="cu-modal" :class="lookImg?'show':''" catchtouchmove="return">
			<view class="cu-dialog">
				<view class="bg-img">
					<image :src="currentImgUrl" class="dialogImg" mode="widthFix"></image>
				</view>
				<view class="cu-bar bg-white">
					<view class="action margin-0 flex-sub" @click="lookImg=false">关闭</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import Evaluate from '@/components/evaluate/rate.vue'
	import { formatTime,http2Https } from "../../common/tools.js"
	export default {
		data() {
			return {
				text:'',
				questionDialog:false,
				text1:'',
				rateDialog:false,
				rateList:[{name:'响应态度',value:0,dim:1},{name:'专业程度',value:0,dim:2}],
				paramsInfo:{
					feedBackId:''
				},
				affixList:[],
				feedbackInfo:{},
				userInfo:{},
				lookImg:false,
				currentImgUrl:'',
				progressList:[],//进度
				adminRole:'',// 1-党支部管理员，2-项目组管理员
			};
		},
		methods:{
			startChange(e,item){
				item.value=e.value
			},
			goRate(){
				this.rateDialog=true
			},
			getInfo(){
				this.request("/feedBack/detail",this.paramsInfo).then((res)=>{
					if(res.resultCode==0){
						var _data=res.data
						switch(_data.feedbackType){
							case 1 : _data.feedbackTypeStr="信息咨询类";break
							case 2 : _data.feedbackTypeStr="改善建议类";break
							case 3 : _data.feedbackTypeStr="服务投诉类";break
							case 4 : _data.feedbackTypeStr="质量投诉类";break
							case 5 : _data.feedbackTypeStr="违规违纪类";break
							default: _data.feedbackTypeStr="- -"
						}
						_data.createTimeStr=formatTime(_data.createTime)
						if(_data.attUrls){
							this.affixList=_data.attUrls.split(',')
							this.affixList.forEach((item)=>{
								item=http2Https(item)
							})
						}
						
						// 遍历 节点
						_data.nodeLogList.forEach((item)=>{
							switch(item.nodeStep){
								case 10 : item.nodeStepStr="党支部受理";break
								case 20 : item.nodeStepStr="问题处理中心受理";break
								case 30 : item.nodeStepStr="业务部门受理";break
								case 40 : item.nodeStepStr="上报公司决策";break
								default : item.nodeStepStr="- -"
							}
							item.opTimeStr=formatTime(item.opTime)
							this.progressList.push(item)
						})
						// 遍历处理进度
						if(_data.processLogList && _data.processLogList.length>0){
							_data.processLogList.forEach((item)=>{
								switch(item.state){
									case -1 : item.nodeStepStr="已取消";break
									case 10 : item.nodeStepStr="党支部已处理";break
									case 20 : item.nodeStepStr="项目组已处理";break
									default : item.nodeStepStr="- -"
								}
								item.opTimeStr=formatTime(item.opTime)
								this.progressList.push(item)
							})
						}
						
						
						switch(_data.userType){
							case 0 :_data.userTypeStr="用户";break
							case 1:_data.userTypeStr="北汽员工";break
							case 2 :_data.userTypeStr="4S店员工";break
							default :_data.userTypeStr="用户";
						}
						
						// 遍历 评分
						_data.evaluateList.forEach((item,index)=>{
							this.rateList[index].value=item.score
						})
						this.feedbackInfo=_data
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			},
			
			makeScore(){
				for(var i=0;i<this.rateList.length;i++){
					if(!this.rateList[i].value){ uni.showToast({title: `请对${this.rateList[i].name}进行评价`,icon:'none'});return}
				}
				var _params={
					feedBackId:this.paramsInfo.feedBackId,
					score1:this.rateList[0].value,
					score2:this.rateList[1].value
				}
				this.request("/feedBack/evalutateFeedback",_params).then((res)=>{
					if(res.resultCode==0){
						uni.showToast({title:res.resultMsg,icon:"none"})
						this.rateDialog=false
						this.getInfo()
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			},
			lookAffix(item){
				if(this.mobileModel=="VOG-AL00"){ //华为p30
					this.currentImgUrl=item;
					this.lookImg=true
				}else{
					//获取 url数组
					var _urlList=[]
					this.affixList.forEach((item)=>{
						_urlList.push(item)
					})
					uni.showLoading()
					uni.previewImage({
						current:item,
						urls: _urlList,
						longPressActions:{
							itemList: ['发送给朋友', '保存图片', '收藏']
						},
						fail(err){
							this.currentImgUrl=item;
							this.lookImg=true
						},
						complete(){
							uni.hideLoading()
						}
					})
				}
			},
		},
		components:{
			Evaluate
		},
		onLoad(params){
			this.paramsInfo.feedBackId=params.feedBackId
			this.getInfo()
			let _userInfo=JSON.parse(uni.getStorageSync('userInfo'));
			this.userInfo=_userInfo
			this.adminRole=_userInfo.role
		}
	}
</script>

